import { createGlobalStyle } from "styled-components";

const COLUMNS = 13;

export const GlobalStyle = createGlobalStyle`
html, body, #root {
  font-family: Noto Sans, Roboto, Open Sans, sans-serif;
  background-color: #F8F5F1;
  max-width: 114rem;
  margin: 0 auto;
  padding: 0 1.25rem;
  @media (min-width: 37.5em) {
    padding: 0 1.875rem;
  }
  @media (min-width: 56.25em) {
    padding: 0 3.125rem;
  }
  @media (min-width: 75em) {
    padding: 0 3.125rem 0 1.25rem; 
    > div {
      /* Calculation of padding is based on 13 columns and column gap
       * where the first column is reserved for sticky header and share icons
       */
      padding-left: calc(100% / ${COLUMNS} + 1.25rem / ${COLUMNS});
    }
  }
}`;
